body{background-color:#1c1c1c;font-family:'Playfair Display SC', serif;-webkit-animation:fade-in 1500ms ease-in;animation:fade-in 1500ms ease-in}.content{display:flex;flex-direction:column;background-color:#1c1c1c;width:100%;padding:5vh;box-sizing:border-box;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}.site-header{align-self:center;font-size:40px;margin-bottom:35px;color:#f35626;background-image:-webkit-linear-gradient(20deg, #f3d426, #1bca1b);background-clip:inherit;-webkit-background-clip:text;-webkit-text-fill-color:transparent;animation:color-change 5s infinite linear;-webkit-animation:color-change 5s infinite linear}.top-row,.bottom-row{display:flex;justify-content:space-between;height:3vh;margin-bottom:35px}.top-row *,.bottom-row *{border-radius:7px}.left-top-row{display:flex;width:35%;justify-content:space-between}#season-dropdown,#stat-dropdown{width:45%;cursor:pointer;border:none;padding:0px 7px;transition:all 300ms ease-in-out}#season-dropdown:hover,#stat-dropdown:hover{transform:scale(1.05)}option{background-color:gray}.not-selected-error{background-color:#af2a2a;color:#ffe100}.not-selected-error.animation{animation:error-shake 400ms linear;-moz-animation:error-shake 400ms linear;-o-animation:error-shake 400ms linear;-webkit-animation:error-shake 400ms linear}.right-top-row{display:flex;width:5%;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}#glossary-button,#information-button{width:100%;cursor:pointer;border:none;padding:0px 7px;transition:all 300ms ease-in-out}#glossary-button:hover,#information-button:hover{transform:scale(1.05)}#glossary-button{margin-left:8%}.search-players-input-container{display:flex;flex-direction:column;width:19.5%;margin-right:7vw;position:relative;box-shadow:0px 0px 4px white;height:4vh;align-self:center}#search-players-input{background:transparent linear-gradient(to right, rgba(201,190,38,0.671), #000);min-height:100%;box-sizing:border-box;border:none;padding:12px;background-color:#1c1c1c;color:white}#search-players-input::-webkit-input-placeholder{color:white}#search-players-input::-moz-placeholder{color:white}#search-players-input:-ms-input-placeholder{color:white}#search-players-input:-moz-placeholder{color:white}#player-dropdown{background-color:lightgray;-webkit-animation-name:fade-in;animation-name:fade-in;-webkit-animation-duration:500ms;animation-duration:500ms;z-index:1;cursor:pointer}#player-dropdown li{min-height:4vh;padding:2px 8px;border-bottom:1px solid black;display:flex;align-items:center;overflow:hidden;box-sizing:border-box}.player-names-container{background:transparent linear-gradient(to left, rgba(201,190,38,0.671), #000);width:75.5%;min-height:5vh;max-height:-webkit-fit-content;max-height:-moz-fit-content;max-height:fit-content;display:flex;color:white;align-items:center;padding:0 1vw;box-sizing:border-box;align-self:center;justify-content:space-evenly;flex-wrap:wrap;box-shadow:0px 0px 4px white}@media only screen and (max-device-width: 500px){.player-names-container{font-size:11px}}.player-name-container{margin-right:1vw;margin-top:1vh;margin-bottom:1vh}.player-name{margin-right:0.4vw}.remove-player-button{border:none;background:none;padding:0;outline:none;color:#f5d90a;font-size:14px}@media only screen and (max-device-width: 500px){.remove-player-button{font-size:9px}}.remove-player-button:hover{cursor:pointer;color:#c5b010}footer{display:flex;justify-content:center;align-items:center;font-size:2.5em}@media only screen and (max-device-width: 500px){footer{font-size:2em}}footer div{margin-right:1.5vw}footer a{color:#dac213}footer a:hover{color:#c02e2e}footer div:last-child{margin-right:0}
